// MODAL :: START

.c-modal {
  #viewAgreement & {
    height: 100%;

    .c-modal {
      &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 140px;
        padding: 50px 60px 40px;
        background-color: #ffffff;
        @include transition(box-shadow);
        @include adaptive(1279) {
          min-height: 100px;
          padding: 20px 40px;
        }
        @include adaptive(767) {
          min-height: 135px;
          padding: 30px 15px;
        }

        &.is-shadow {
          box-shadow: 0px 11px 22px rgba(72, 130, 223, 0.1);
          @include transition(box-shadow);
        }
      }

      &__body {
        position: relative;
        height: calc(100% - 190px);
        @include adaptive(1279) {
          height: calc(100% - 140px);
        }
        @include adaptive(767) {
          height: calc(100% - 165px);
        }

        &.is-bottom {
          &:after {
            opacity: 0;
            visibility: hidden;
            @include transition(opacity, visibility);
          }
        }

        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 100px;
          background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
          @include transition(opacity, visibility);
        }

        &-scroll {
          overflow-y: auto;
          position: relative;
          height: 100%;
          padding-left: 60px;
          padding-right: 60px;
          @include adaptive(1279) {
            padding-left: 40px;
            padding-right: 40px;
          }
          @include adaptive(767) {
            padding-left: 15px;
            padding-right: 15px;
          }
        }
      }

      &__title {
        font-size: 40px;
        font-weight: 900;
        line-height: 1.25;
        color: #000000;
        @include adaptive(1279) {
          font-size: 36px;
        }
        @include adaptive(767) {
          font-size: 28px;
        }
      }

      &__close {
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          width: 26px;
          height: 26px;
          fill: #000;
          @include adaptive(1279) {
            width: 22px;
            height: 22px;
          }
          @include adaptive(767) {
            width: 20px;
            height: 20px;
          }
        }
      }

      &__info {
        display: flex;
        flex-direction: column;
        padding-bottom: 30px;

        &-text {
          display: flex;
          align-items: center;
          font-size: 19px;
          font-weight: 900;
          line-height: 1.58;
          color: #000000;
          @include adaptive(1279) {
            font-size: 17px;
          }
          @include adaptive(767) {
            flex-wrap: wrap;
            font-size: 15px;
          }

          span {
            font-weight: normal;
            @include adaptive(767) {
              width: 100%;
            }
          }

          a {
            font-weight: normal;
            color: #00449d;
            @include adaptive(767) {
              width: 100%;
            }
          }
        }
      }

      &__desc {
        padding-bottom: 50px;
        font-size: 19px;
        line-height: 1.58;
        color: #000000;
        @include adaptive(1279) {
          font-size: 17px;
        }
        @include adaptive(767) {
          font-size: 15px;
        }

        span {
          font-weight: 900;
        }
      }

      &__subtitle {
        padding-top: 30px;
        padding-bottom: 20px;
        font-size: 26px;
        font-weight: 900;
        line-height: 1.15;
        color: #000000;
        @include adaptive(1279) {
          font-size: 22px;
        }
        @include adaptive(767) {
          font-size: 19px;
        }

        &--first {
          padding-top: 0;
        }
      }

      &__text {
        padding-bottom: 20px;
        font-size: 19px;
        line-height: 1.58;
        color: #000000;
        @include adaptive(1279) {
          font-size: 17px;
        }
        @include adaptive(767) {
          font-size: 15px;
        }

        a {
          color: #00449d;
        }
      }

      &__list {
        padding-left: 40px;
        @include adaptive(1279) {
          padding-left: 20px;
        }
        @include adaptive(767) {
          padding-left: 15px;
        }

        li {
          padding-bottom: 20px;
          font-size: 19px;
          line-height: 1.58;
          color: #000000;
          @include adaptive(1279) {
            font-size: 17px;
          }
          @include adaptive(767) {
            font-size: 15px;
          }

          a {
            color: #00449d;
          }

          span {
            font-weight: 900;
            text-transform: uppercase;
          }
        }
      }

      &__input {
        input {
          outline: none;
          width: 100%;
          height: 60px;
          padding: 0 20px;
          border-radius: 10px;
          border: none;
          box-shadow: none;
          background-color: rgba(0, 0, 0, 0.05);
          font-size: 20px;
          line-height: 1.5;
          color: rgba(#000000, 0.5);
          @include inputPlaceholder {
            font-size: 20px;
            line-height: 1.5;
            color: rgba(#000000, 0.5);
          }
        }

        &-wrapper {
          display: flex;
          justify-content: space-between;
          padding-top: 10px;
          padding-bottom: 50px;
          @include adaptive(767) {
            flex-wrap: wrap;
          }
        }
        
        &-left,
        &-right {
          width: 100%;
          max-width: calc((100% - 40px) / 2);
          @include adaptive(767) {
            max-width: 100%;
          }
        }

        &-left {
          @include adaptive(767) {
            padding-bottom: 8px;
          }
        }

        &-title {
          padding-bottom: 8px;
          font-size: 16px;
          font-weight: 600;
          line-height: 1.88;
          color: rgba(#000000, 0.5);
        }
      }
      
      &__btn {
        display: flex;
        align-items: center;
        padding-bottom: 30px;
        @include adaptive(767) {
          flex-wrap: wrap;
        }
        
        > a {
          width: auto;
          max-width: unset;
          padding-left: 50px;
          padding-right: 50px;
          margin-right: 40px;
          @include adaptive(767) {
            width: 100%;
            max-width: 100%;
            height: 60px;
            margin-right: 0;
          }

          &.c-btn--bg {
            box-shadow: 0 10px 15px 0 rgba(205, 8, 75, 0.2);
            @include transition(color, box-shadow, background-color);

            &:hover {
              color: #cd084b;
              box-shadow: none;
              @include transition(color, box-shadow, background-color);
            }
          }

          &:last-of-type {
            margin-right: 0;
            @include adaptive(767) {
              margin-top: 15px;
            }
          }
        }
      }
    }
  }

