            height: 60px;
          }
        }
      }

      &__table {
        &-head {
          overflow: hidden;
          z-index: 10;
          position: relative;
          border-radius: 10px;
          background-color: #f8fafc;

          .c-modal__table-th {
            min-height: 60px;
            padding: 5px 10px 5px 30px;
            @include adaptive(767) {
              padding-left: 5px;
              padding-right: 5px;
            }
          }
        }

        &-body {
          .c-modal__table {
            &-tr {
              border-bottom: 1px solid darken(#f8fafc, 5%);

              &:first-of-type {
                > div {
                  align-items: flex-start;
                  min-height: 59px;
                }
              }
              &:last-of-type {
                border-bottom: none;
              }
            }

            &-td {
              min-height: 89px;
              padding: 5px 10px 5px 50px;
              @include adaptive(767) {
                padding-left: 5px;
                padding-right: 5px;
              }

              &:nth-of-type(1) {
                padding-left: 10px;
                @include adaptive(767) {
                  padding-left: 5px;
                }
              }
            }
          }
        }

        &-footer {
          padding-top: 30px;
          padding-bottom: 60px;
        }

        &-tr {
          display: flex;
          align-items: center;
        }

        &-th,
        &-td {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          flex: 1;
          min-width: 1px;
          max-width: 100%;
          @include adaptive(767) {
            text-align: center;
            justify-content: center;
          }
        }

        &-th {
          p {
            font-size: 19px;
            font-weight: bold;
            line-height: 1.58;
            color: #000000;
            @include adaptive(1023) {
              font-size: 17px;
            }
            @include adaptive(575) {
              font-size: 14px;
            }
            @include adaptive(374) {
              font-size: 12px;
            }
          }
        }

        &-td {
          span {
            font-size: 19px;
            line-height: 1.58;
            color: #000000;
            @include adaptive(1023) {
              font-size: 17px;
            }
            @include adaptive(424) {
              font-size: 14px;
            }
            @include adaptive(374){
              font-size: 12px;
            }
          }
        }
      }
    }
  }

