// SECTION scheduler :: START

&__time {
  display: none;
  padding-top: 120px;
  padding-bottom: 140px;
  background-color: #f8fafc;
  @include adaptive(767) {
    padding-top: 60px;
    padding-bottom: 70px;
  }

  &-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &-1 {
      padding-bottom: 60px;
      @include adaptive(767) {
        padding-bottom: 20px;

        .scheduler__time-col {
          max-width: 100%;
        }
      }
    }
  }

  &-col {
    width: 100%;
    max-width: calc((100% - 40px) / 2);
    @include adaptive(767) {
      max-width: calc((100% - 20px) / 2);
    }
  }

  &-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 60px;
    margin-bottom: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
    @include transition(box-shadow, background-color);
    @include adaptive(767) {
      margin-bottom: 10px;
    }

    &:last-of-type {
      margin-bottom: 0;
    }

    &.is-active,
    &:hover {
      box-shadow: none;
      background-color: #00449d;
      @include transition(box-shadow, background-color);

      span {
        color: #ffffff;
        @include transition(color);
      }
    }

    span {
      font-size: 20px;
      line-height: 30px;
      text-align: center;
      color: #000;
      @include transition(color);
      @include adaptive(767) {
        font-size: 16px;
        line-height: normal;
      }
    }
  }

  .c-form {
    &__label {
      color: rgba(#000, 0.5);
    }

    &__select {
      border: 2px solid rgba(0, 0, 0, 0.05);
    }
  }
}

// SECTION scheduler :: END